import React, { useState } from 'react'
import { Navbar, Nav, Form, FormControl, Button } from 'react-bootstrap'
import { BrowserRouter, Route, Link } from 'react-router-dom'
import Home from "./components/home"
import Exchanges from './components/exchanges'

function App() {
  const [search, setSearch] = useState("new");
  const [isSearching, setIsSearching] = useState(false);
  const [searching, setSearching] = useState("new");

  const submitQuery = e => {
    e.preventDefault();
    setSearch(e.target[0].value);
    setIsSearching(true);
    setSearching(e.target[0].value);
  }
  const disableSearching = () => {
    setIsSearching(false);
    setSearch("new");
  }

  return (
    <BrowserRouter>
      <div className="App">
        <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
          <Link to="/news" className="navbar-brand" onClick={disableSearching}>Home</Link>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="links">
              <Link to="/World" className="nav-link" onClick={disableSearching}>World</Link>
              <Link to="/Politics" className="nav-link" onClick={disableSearching}>Politics</Link>
              <Link to="/Business" className="nav-link" onClick={disableSearching}>Business</Link>
              <Link to="/Health" className="nav-link" onClick={disableSearching}>Health</Link>
              <Link to="/Entertainment" className="nav-link" onClick={disableSearching}>Entertainment</Link>
              <Link to="/Style" className="nav-link" onClick={disableSearching}>Style</Link>
              <Link to="/Travel" className="nav-link" onClick={disableSearching}>Travel</Link>
              <Link to="/Sport" className="nav-link" onClick={disableSearching}>Sport</Link>
            </Nav>
          </Navbar.Collapse>
          <Form inline onSubmit={submitQuery}>
            <FormControl type="text" placeholder="Search" className="mr-sm-2" />
            <Button variant="outline-info" >Search</Button>
          </Form>
        </Navbar>
        <Exchanges></Exchanges>
      </div>
      <Route exact path="/kNews-react" render={() => <Home search={search} isSearching={isSearching} searching={searching} />} />
      <Route path="/news" render={() => <Home search="new" isSearching={isSearching} searching={searching} />} />
      <Route path={`/${search}`} render={() => <Home search={search} isSearching={isSearching} searching={searching} />} />
      <Route path="/World" render={() => <Home search={"World"} isSearching={isSearching} searching={searching} />} />
      <Route path="/Politics" render={() => <Home search={"Politics"} isSearching={isSearching} searching={searching} />} />
      <Route path="/Business" render={() => <Home search={"Business"} isSearching={isSearching} searching={searching} />} />
      <Route path="/Health" render={() => <Home search={"Health"} isSearching={isSearching} searching={searching} />} />
      <Route path="/Entertainment" render={() => <Home search={"Entertainment"} isSearching={isSearching} searching={searching} />} />
      <Route path="/Style" render={() => <Home search={"Style"} isSearching={isSearching} searching={searching} />} />
      <Route path="/Travel" render={() => <Home search={"Travel"} isSearching={isSearching} searching={searching} />} />
      <Route path="/Sport" render={() => <Home search={"Sport"} isSearching={isSearching} searching={searching} />} />
      <footer>
        <p>Posted by: K</p>
        <p>Contact information: <a href="mailto:someone@example.com">
          someone@example.com</a>.</p>
      </footer>
    </BrowserRouter>

  );
}

export default App;
