import React from 'react'

const NewPage = (props) => {
    // {alert("ASD")}
    console.log(props.article);
    const article = props.article;
    return (
        <div className="article-container">
            <div className="article">
                <div className="article-header">
                    <div className="article-title">
                        <h1>{article.title}</h1>
                    </div>
                    <div className="article-info">
                        <div className="article-date-author">
                            <div className="article-date">
                                <span> <i className="fa fa-clock-o"></i>{` ${article.publishedAt.slice(0, 10)} ${article.publishedAt.slice(11, 19)}`}</span>
                            </div>
                            <div className="article-author">
                                <span>{`Author: ${article.author}`}</span>
                            </div>
                        </div>
                        <div className="article-icons">
                            <img src="https://image.flaticon.com/icons/svg/174/174848.svg" alt="fb-icon"/>
                            <img src="https://image.flaticon.com/icons/svg/174/174876.svg" alt="twitter-icon"/>
                            <img src="https://image.flaticon.com/icons/svg/174/174879.svg" alt="wp-icon"/>
                            <img src="https://image.flaticon.com/icons/svg/174/174851.svg" alt="gp-icon"/>
                        </div>
                    </div>
                </div>
                <div className="article-body">
                    <img src={article.urlToImage} alt={article.title}></img>
                    <span className="article-description">
                        {article.content}
                    </span>
                    <a href={article.url}>View article</a>
                </div>
            </div>
        </div>
    )
}
export default NewPage
